import axios from '@src/public/utils/request';
import qs from 'qs';

// yapi
// 
export const getMySpillList = ({ page = 1, size = 20, status }) => {
    // 
    const data = {
        pagination: {
            page,
            size,
        },
        query: {
            // all/1/0/-1
            status,
        },
    };

    // ios12Bug
    return axios.post(
        'https://api.ifengcloud.ifeng.com/mock/1749/timeline/front/api/exposures/list',
        qs.stringify(data),
        {
            headers: {
                'Content-Type': 'application/x-www-form-urlencoded',
            },
        },
    );

    // 
    // return axios.post('/front/api/exposures/list', data)
};

