import { useState, useEffect } from 'react';
import { Store } from '../../store';
// API
import { getMySpillList } from '../../service';
// 
const useList = () => {
    // 
    const store = Store.useContainer();
    const { states, changeStates } = store;
    const {
        // 
        nodes,
        // 
        parties, // 
        status, // 
        // 
        page,
        size,
        total,
    } = states;

    // 
    useEffect(
        () => {
            // /1/0/-1
            changeStates(
                {
                    parties,
                },
                () => {},
            );
        },
        [status],
    );

    // 
    const control = async () => {
        try {
            if ((page - 1) * size < total) {
                const res = await getMySpillList({ page, size, status });

                changeStates({
                    // 
                    nodes,
                    // 
                    parties: [...parties, ...res.nodes], // (UI)
                    // 
                    page: +page + 1,
                    size,
                    total,
                });

                return 'success';
            } else {
                console.log('');

                return 'end';
            }
        } catch (e) {
            console.error(e);
        }
    };

    // 

    return {
        nodes, // 
        parties, // 
        status, // 
        page, // 
        size, // 
        total, // 
        control,
    };
};

export { useList };
