import React, { useMemo } from 'react';
import styles from './index.css';
import errorBoundary from '@src/components/errorBoundary';
// 
import { useList } from './hooks/useList';
// 
import Item from './Item';
// 
// import LoadingMore from '../../../components/LoadingMore';
// 
import Spin from '../Spin';
// 
import NoData, { noDataEnum } from '../Nodata';
import { Store } from '../store';

// UI
const List = () => {
    // 
    const store = Store.useContainer();
    const { states } = store;
    const { isLoading } = states;

    // 
    const {
        // 
        nodes,
        // 
        parties, // 
        status,
        // 
        control,
    } = useList();

    // UI
    const uiContent = () => {
        return nodes && nodes.length ? (
            <>
                <div className={styles.list}>{parties.map((item, index) => <Item key={index} data={item} />)}</div>
                {/*  */}
                {/* {parties.length > 0 ? <LoadingMore control={control} key={status} /> : null} */}
            </>
        ) : (
            <NoData type={noDataEnum.NOSEARCH}>
                
                <div style={{ color: '#3091f5' }}> &gt;&gt;</div>
                
            </NoData>
        );
    };

    return (
        <div className={styles.wrap}>
            {// 
            isLoading ? (
                <Spin spinning={true}>
                    <div className={styles.spin_place} />
                </Spin>
            ) : (
                uiContent() // UI
            )}
        </div>
    );
};

export default errorBoundary(List);
